import React from 'react'
import Table from 'react-bootstrap/Table';
import { Container } from 'react-bootstrap';
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'

class TransactionTable extends React.Component {

    render() {
        let rows = [];
            let values = Object.values(this.props.data);
            rows = [].concat(values)
            .sort((a, b) => a.recievedTimeForDisplay.localeCompare(b.recievedTimeForDisplay))
            .map((item, i) => 
                this.getTxnObject(item, i+1)
            );

            if(rows.length === 0) {
                rows.push(<tr key='no_data'><td colSpan={5}><center>No data</center></td></tr>)
            }

        return(
        <Container fluid>
            <Table striped bordered hover responsive varient="dark">
                <thead>
                    <tr key='title_1'>
                    <th>#</th>
                    <th>Source/Transaction ID</th>
                    <th>Transaction Status</th>
                    <th>Data</th>
                    <th>Timing</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </Table>
        </Container>
        );
    }

    getTxnObject(txmObject,count) {
        let exceptionObj = '';
        let stack = txmObject['exceptionStack'];
        let lt;
        if(stack != null) {
            for(lt of stack){
                exceptionObj+= <br>{lt}</br>
            }
            exceptionObj =  (<Accordion defaultActiveKey="5" fluid style={{minWidth: '18rem', maxWidth:'30rem'}}>
                            <Card>
                                <Card.Header>
                                    <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                        Exception Stack
                                    </Accordion.Toggle>
                                </Card.Header>
                                <Accordion.Collapse eventKey="0">
                                    <Card.Body fluid>
                                        {stack.map((m,i) => <span key={'message-${i}'}><i>{m}</i><br /></span>)}
                                        ......
                                    </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>)
        }
        return(<tr key={txmObject.txnId} style={{maxHeight:'10rem'}}>
            <td>{count}</td>
            <td>
                <TransactionId value={txmObject}></TransactionId>
            </td>
            <td>
                {txmObject.status}
            </td>
            <td style={{minWidth: '18rem', maxWidth:'33rem', maxHeight:'20rem'}}>
                <ul>
                    <li>
                        Module Name : <b>{txmObject.remoteActionMessageBean.moduleName} </b>
                    </li>
                    <li>
                        Action : <b>{txmObject.remoteActionMessageBean.actionName}</b>
                    </li>
                </ul>
                <Accordion defaultActiveKey="5" style={{minWidth: '18rem', maxWidth:'30rem'}}>
                        <Card>
                            <Card.Header>
                                <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                    Incoming Message
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body><JSONDisplayer>{txmObject.inbound}</JSONDisplayer></Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                                <Accordion.Toggle as={Button} variant="link" eventKey="1">
                                    Reply Message
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                                <Card.Body>
                                    <JSONDisplayer>
                                        {txmObject.remoteActionMessageBean.message}
                                    </JSONDisplayer>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>

                    <Accordion defaultActiveKey="5" style={{minWidth: '18rem', maxWidth:'30rem'}}>
                        <Card>
                            <Card.Header>
                                <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                    Message info
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    <ul>
 
                                        <li>
                                            Connection ID : {txmObject.remoteActionMessageBean.connectionId} 
                                        </li>
                                        <li>
                                            Sync Txn : {txmObject.remoteActionMessageBean.sync.toString()}
                                        </li>
                                        <li>
                                            User ID : {txmObject.remoteActionMessageBean.userId} 
                                        </li>
                                        <li>
                                        Message Fate : {txmObject.remoteActionMessageBean.status} 
                                        </li>
                                    </ul>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>

                    {exceptionObj}
  
            </td>
            <td>
                <ul>
                    <li>
                        Start time : {txmObject.recievedTimeForDisplay} 
                    </li>
                    <li>
                        End time : {txmObject.processingCompleteTimeForDisplay}
                    </li>
                    <li>
                        Processing duration : {txmObject.timeTookForProcessing} 
                    </li>
                </ul>
            </td>
        </tr>);
    }

}

function TransactionId({value}) {
    let fontColor = 'black';
    if(value.remoteActionMessageBean.status === 'FAILED') {
        fontColor = 'red';
    }
    return(<span style={{color: fontColor}}>{value.sourceServer} <br/>{value.txnId}</span>)
}

const getJsonIndented = (obj) => JSON.stringify(obj, null, 4).replace(/["{[,\}\]]/g, "")

const JSONDisplayer = ({children}) => (
    <div>
        <pre>{getJsonIndented(children)}</pre>
    </div>
)


export default TransactionTable;