import React from 'react'
import Table from 'react-bootstrap/Table';
import { Container } from 'react-bootstrap';

class ClientList extends React.Component {

    render() {
        let rows = [];

        if(this.props.data) {
            //let values = Object.values(this.props.data);
            //let obj = {"127.0.0.1":4,"256.32.15.4":2};
            let data = this.props.data;
            let values = Object.keys(this.props.data);
            values.forEach((k,i) => {
                rows.push(<tr key={k} style={{maxHeight:'10rem'}}>
                            <td>{i+1}</td>
                            <td>{k}</td>
                            <td>{data[k]}</td>
                        </tr>);
            });

            if(rows.length === 0) {
                rows.push(<tr key='no_data'><td colSpan={5}><center>No data</center></td></tr>)
            }
        } else {
            rows.push(<tr key='no_data'><td colSpan={5}><center>No data</center></td></tr>)
        }

        return(
        <Container fluid>
            <Table striped bordered hover responsive varient="dark">
                <thead>
                    <tr key='title_1'>
                        <th>#</th>
                        <th style={{width: '50%'}}>Client IP</th>
                        <th>No of active connections</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </Table>
        </Container>
        );
    }

}

export default ClientList;